<template>
  <div class="yao4">
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>诊所合作</el-breadcrumb-item>
    </el-breadcrumb>

    <ve-line :data="chartData" :settings="chartSettings"></ve-line>
    


  </div>
</template>

<script>
export default {
  data() {
    return {
    };
  },
  created: function () {
      this.chartData = {
        columns: ['date', '销量'],
        rows: [
          { 'date': '1月', '销量': 0 },
          { 'date': '2月', '销量': 0},
          { 'date': '3月', '销量': 80 },
          { 'date': '4月', '销量': 90},
          { 'date': '5月', '销量': 100},
          { 'date': '6月', '销量': 80 },
          { 'date': '7月', '销量': 70 },
          { 'date': '8月', '销量': 60 },
          { 'date': '9月', '销量': 10 },
          { 'date': '10月', '销量': 20 },
          { 'date': '11月', '销量': 10 },
          { 'date': '12月', '销量': 30 },
        ]
      }
      this.chartSettings = {
        label: {
          normal: {
            show: true
          }
        }
      }
    }
};
</script>

<style lang="scss" scoped>
.center-bell{
  padding: 15px;
  a:last-child{
    margin-bottom: 0;
  }
  a{
    background: #eee;
    display: block;
    padding: 10px;
    margin: 10px 0;
    color: #461b93;
  }
}


</style>



